.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.flexbox-fix {
  position: relative;
  height: 500px;
}
.HSV {
  position: absolute;
  bottom: 180px;
  font-size: 20px;
}
.HSV input {
  height: 15px;
  margin-left: 5px;
  margin-right: 5px;
}
@keyframes heart{
  0% {
    transform: scale(1) rotate(45deg)
  }
  50%{
    transform: scale(1.1) rotate(45deg)
    
  }
  100%{
    transform: scale(1) rotate(45deg)
  }
}

.heart {
  margin-left: 45px;
  margin-top: 20px;
  width: 50px; 
  height: 50px; 
  display:inline-block;
  animation: heart 1s ease infinite;
  transform: rotate(45deg);
  position: relative; }
  .heart::before,
  .heart::after {
      content : ''; 
      display: block;
      width: 50px;
      height: 50px;
      border-radius: 50%; 
      position: absolute;
  }
  
  .heart::before {
    top: -31px;
    left: 0;
}
.heart::after {
  top: 0;
  left: -31px;
}   
.HSL {
  position: absolute;
  bottom: 150px;
  font-size: 20px;
}
.HSL input {
  height: 15px;
  margin-left: 5px;
  margin-right: 5px;
}
.HSL__span{
  margin-right: 5px
}